body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (max-width : 450px){
  .slap-counter{
    margin-top: 10px;
  }
  .animation-target >img {
    height: 200px;
    width: 200px;
    cursor: pointer;
  }
  .animation-target {
    animation: animation 865ms linear both;
    background: transparent;
    position: absolute;
    margin-top: 60px;
    z-index: 99;
    cursor: pointer;
  }
  .upload-file{
    border: dotted;
    border-color: whitesmoke;
    text-align: center;
    cursor: pointer;
    width: 100%;
  }
  .label-file-upload{
    text-align: center;
    padding: 10px;
    display: block;
    border: dashed;
    border-color: whitesmoke;
  }
  .btn-tampol{
    display: none !important;
  }

  .enemy-foto{
    width: 100%;
    min-height: 300px;
  }
}

@media (min-width : 800px){
  .slap-counter{
    margin-top: 200px;
  }
  .animation-target >img {
    height: 300px;
    width: 300px;
    cursor: pointer;
  }
  .animation-target {
    animation: animation 865ms linear both;
    background: transparent;
    position: absolute;
    margin-top: 77px;
    z-index: 99;
    cursor: pointer;
    position: absolute;
  }
  .upload-file{
    border: dotted;
    border-color: whitesmoke;
    text-align: center;
    cursor: pointer;
    width: 100%;
  }
  .label-file-upload{
    text-align: center;
    padding: 10px;
    display: block;
  }
  .enemy-foto{
    width: 100%;
    min-height: 400px;
  }
  .label-file-upload{
    text-align: center;
    padding: 10px;
    display: block;
    border: dashed;
    border-color: whitesmoke;
  }
  .container-fluid{
    margin-top: 20px ;
  }
}




.card-body{
  padding:0px !important;
}

